.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 160px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.component {
  margin: 0 20px;
  display: inline;
}

.rate-product {
  max-width: 500px;
  margin: 0 auto;

  margin-top: 10px;
  margin-bottom: 50px;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 30px;
  -webkit-box-shadow: #ccccccc4 1px 2px 7px 2px;
          box-shadow: #ccccccc4 1px 2px 7px 2px;
}
.product {
  display: -ms-flexbox;
  display: flex;
}
.product__info {
  text-align: left;
  -ms-flex-align: center;
      align-items: center;
}
.product__description {
  color: #9e9e9e;
  font-style: italic;
  font-size: 14px;
}
.product__bought__date {
  margin-top: 38px;
  font-size: 12px;
  color: #717171;
}

.rating {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-left: 50px;
}

.rating__description {
  margin-right: 15px;
}

.btn.btn-blue {
  background-color: #60dbfb;
  padding: 10px;
  border: 0;
  border-radius: 16px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  outline: none;
}

.btn.btn-blue:hover {
  background-color: #43d8ff;
}

.btn.btn-blue.disabled {
  background-color: #ddd;
  pointer-events: none;
  opacity: .7;
}


